/* button style */
.owl-carousel {
  .owl-nav div {
    background: #f2fcff;
    height: 60px;
    left: 0px;
    line-height: 60px;
    // opacity: 0;
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    // visibility: hidden;
    width: 60px;
    font-size: 12px;
    color: #c7cfd2;
    @include border-radius(50%);
  }
  .owl-nav {
    div {
      &.owl-next {
        // left: auto;
        // right: 0px;
      }
      &.owl-prev {
        // i{
        // 	position: relative;
        // 	right: 2px;
        // }
      }
    }
  }
  &:hover {
    .owl-nav {
      div {
        opacity: 1;
        visibility: visible;
        &:hover {
          color: $color_2;
        }
      }
    }
  }
}

