.owl-dots {
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  bottom: 73px;
  right: 0;
  grid-gap: 22px;
  .owl-dot {
    margin: 0 5px;
    span {
      width: 11px;
      height: 11px;
      border-radius: 30px;
      background: #d9e3e3;
      display: inline-block;
      transition: 0.3s;
      position: relative;

      &::before{
        content: '';
        position: absolute;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 1px solid #fff;
        left: -7px;
        top: -7px;
      }
      &:hover {
        transform: scale(1.2);
      }
    }
    &.active {
      span {
        // width: 30px;
        background: $purple_color ;
        &::before{
          border-color: $purple_color ;
        }
      }
    }
  }
}
