.nice-select {
  &::after {
    border-bottom: 1px solid #687083;
    border-right: 1px solid #687083;
    height: 10px;
    margin-top: 3px;
    width: 10px;
  }
  .nice-select-search-box {
    padding: 30px 30px 0 30px;
    max-width: 100%;
    border-radius: 5px !important;
    border: 1px solid #e9e7f7;
    border-bottom: 0;
    margin-top: 16px;
    border-radius: 0 !important;
    @media #{$tab} {
      width: 100%;
    }
    &::before {
      content: "";
      position: absolute;
      left: 25px;
      transform: translateX(0%) rotate(-130deg);
      width: 25px;
      height: 25px;
      background: #fff;
      border-bottom: 1px solid #eceef4;
      z-index: 1;
      top: -13px;
      border-right: 1px solid #eceef4;
    }
    &::after {
      content: "\e610";
      font-family: "themify";
      color: $purple_color ;
      font-size: 18px;
      position: absolute;
      left: 50px;
      top: 32px;
    }
    input {
      background: #f9f9fd;
      font-size: 14px;
      font-weight: 500;
      color: $theme_color;
      font-family: $font1;
      background: #f9f9fd;
      border-radius: 5px;
      height: 50px;
      padding-left: 48px;
      border: 0 !important;
      &::placeholder {
        color: #687083;
        font-weight: 500;
      }
    }
  }
  ul.list {
    max-width: 436px;
    border: 1px solid #e9e7f7;
    margin-top: 18px;
    overflow-y: auto !important;
    margin-top: 95px;
    box-shadow: none !important;
    padding: 15px 0 19px 0;
    border-radius: 0 !important;
    border-top: 0 !important;
    @media #{$tab} {
      width: 100%;
    }
    li.option {
      font-size: 16px;
      font-weight: 600;
      color: #202e3b;
      font-family: $font1;
      padding: 0px 30px;
      background: transparent;
      &.focus,
      &:hover,
      &.selected.focus {
        color: $purple_color;
        background: $white;
      }
    }
  }
}
.theme_search_field {
  background: #f9f9fd;
  height: 50px;
  border-radius: 5px;
  width: 400px;
  button {
    font-size: 18px;
    color: $purple_color;
    padding: 0 0 0 15px;
    line-height: 100%;
    box-shadow: none !important;
    &:hover {
      color: $purple_color;
    }
  }
  input {
    font-size: 14px;
    font-weight: 500;
    color: #687083;
    font-family: $font1;
    border: 0;
    background: transparent !important;
    height: 50px;
    padding: 0px 15px 0px 10px;
    box-shadow: none !important;
    line-height: 50px;
  }
  &.large_search_field {
    width: 500px;
    height: 60px;
    button {
      padding-left: 30px;
    }
    input {
      height: 60px;
      line-height: 60px;
    }
  }
  &.style2 {
    border: 1px solid #e9e7f7;
    border-radius: 5px;
    background: $white;
    width: 376px;
    @media #{$tab} {
      width: 280px;
    }
  }
}

