.cs_modal {
  background-color: #f6f8fa;
  .form-control:focus {
    box-shadow: none;
  }
  .modal-header {
    background-color: $white;
    padding: 23px 30px;
    border-bottom: 0px solid transparent;
    h5 {
      font-size: 22px;
      font-weight: 600;
    }
    span {
      font-size: 14px;
      color: #707070;
      opacity: 1;
    }
  }
  .modal-body {
    padding: 35px 30px;
    input,
    .nice_Select {
      height: 50px;
      line-height: 50px;
      padding: 10px 20px;
      border: 1px solid #f1f3f5;
      color: #707070;
      font-size: 14px;
      font-weight: 500;
      background-color: $white;
      width: 100%;
    }
    .nice_Select {
      line-height: 29px;
      &:after {
        right: 22px;
        top: 35%;
        transform: translateY(-50%);
      }
      .list {
        width: 100%;
      }
    }
    .form-group {
      margin-bottom: 12px;
    }
    textarea {
      height: 168px;
      padding: 15px 20px;
      border: 1px solid #f1f3f5;
      color: #707070;
      font-size: 14px;
      font-weight: 500;
    }
    p {
      font-size: 16px;
      font-weight: 500;
      margin-top: 25px;
      color: #707070;
      text-align: center;
      a {
        color: $theme_color;
      }
    }

    .btn_1 {
      width: 100%;
      display: block;
      margin-top: 20px;
    }
    .social_login_btn {
      .btn_1 {
        color: $white;
        background-color: #3b5998;
        border: 1px solid #3b5998;
        margin-top: 0;
        &:hover {
          color: $white !important;
        }
        i {
          margin-right: 10px;
        }
      }
      .form-group {
        &:last-child {
          .btn_1 {
            background-color: #4285f4;
            border: 1px solid #4285f4;
          }
        }
      }
    }
    .pass_forget_btn {
      color: #707070;
      margin-top: 20px;
      font-size: 16px;
      text-align: center;
      font-weight: 500;
      display: inline-block;
      &:hover {
        color: $theme_color;
      }
    }
  }
}

.instructor_cta {
  @media (min-width: 576px) {
    .modal-dialog {
      max-width: 625px;
      margin: 1.75rem auto;
    }
  }
}
.modal-header .close {
  opacity: 1;
  text-shadow: none;
  padding: 0;
  margin: 0;
  margin-top: 3px;
}
.theme_pagination {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 16px;
  justify-content: center;
  a {
    width: 48px;
    height: 48px;
    border-radius: 0%;
    border: 1px solid #D6D6D6;
    font-size: 20px;
    font-weight: 400;
    color: #666666;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: $font1;
    &.active,
    &:hover {
      background: $purple_color;
      color: $white;
      border-color: $purple_color;
      i {
        color: $white !important;
      }
    }
    &.next_pagination {
      border-color: $purple_color;
      i {
        font-size: 20px;
        color: $purple_color;
      }
    }
  }
}

.border_line{
  background: #392C7D;
  height: 2px;
  width: 80px;
  display: inline-block;
}