.course_area{
    .single_course{
        .thumb{
            position: relative;
            overflow: hidden;
            img{
                transform: scale(1);
                transition: .3s;
            }
            .prise_tag{
                position: absolute;
                width: 60px;
                height: 60px;
                line-height: 60px;
                text-align: center;
                font-size: 14px;
                font-weight: 700;
                top: 20px;
                left: 20px;
                border-radius: 50%;
                background: $white;
                color: #FB1159;
            }
        }
        .course_content{
            padding-top: 26px;
            padding-right: 20px;
            h4{
                font-weight: 700;
                transition: .3s;
                &:hover{
                    color:$purple_color ;
                }
            }
            .rating_cart{
                display: flex;
                margin-bottom: 30px;
                margin-top: 10px;
                .rateing{
                    border: 1px solid #F5F5F5;
                    font-size: 14px;
                    font-weight: 500;
                    color: $theme_color;
                    padding: 0px 16px;
                    display: inline-flex;
                    align-items: center;
                    height: 40px;
                    margin-right: 5px;
                    span{
                        margin-right: 7px;
                    }
                    i{  
                        color: #FFC107;
                    }
                }
                .cart_store{
                    border: 1px solid #F5F5F5;
                    font-size: 14px;
                    color: $theme_color;
                    width: 40px;
                    height: 40px;
                    text-align: center;
                    line-height: 40px;
                    display: inline-block;
                    transition: .3s;
                    &:hover{
                        border-color:$purple_color ;
                        color:$purple_color ;
                    }
                }
            }
            .course_less_students{
                a{
                    font-size: 14px;
                    font-weight: 400;
                    color: $theme_color;
                    display: inline-flex;
                    align-items: center;
                    &:not(:last-child){
                        margin-right: 30px;
                    }

                    i{
                        font-size: 18px;
                        font-weight: 400;
                        margin-right: 10px;
                    }
                }
            }
        }
        &:hover{
            img{
                transform: scale(1.1);
            }
        }
    }
}

