.primary_checkbox {
  display: inline-block;
  position: relative;
  margin: 0;
  align-items: center;
  h5 {
    font-size: 14px;
    font-weight: 500;
    font-family: $font2;
    color: $theme_color;
    margin-bottom: 0;
  }
  .label_name {
    font-size: 14px;
    font-weight: 500;
    font-family: $font2;
    color: $theme_color;
    margin-bottom: 0;
  }
  &.white_checkbox {
    .checkmark:after {
      border-color: $white;
    }
  }
  .checkmark {
    position: relative;
    width: 18px;
    height: 18px;
    top: 0;
    left: 0;
    display: block;
    cursor: pointer;
    line-height: 18px;
    flex: 18px 0 0;
    border-radius: 3px;
  }
  // .checkmark:after {
  //     content: "";
  //     position: absolute;
  //     display: none;
  // }
  input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
  }
  input:checked ~ .checkmark:after {
    // background: linear-gradient(90deg, #7C32FF 0.47%, #C738D8 100%);
    width: 100%;
    height: 100%;
    border: 0;
    // box-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25);
    transition: 0.3s;
    transform: scale(0);
  }
  input:checked ~ .checkmark {
    background: $purple_color;
    // width: 100%;
    // height: 100%;
    transition: 0.3s;
  }
  input:checked ~ .checkmark::before {
    content: "\e64c";
    font-family: "themify";
    position: absolute;
    display: block;
    top: 0px;
    left: 3px;
    text-indent: 1px;
    color: #828bb2;
    background-color: transparent;
    border: 0px;
    -webkit-transform: rotate(8deg);
    -moz-transform: rotate(8deg);
    -ms-transform: rotate(8deg);
    -o-transform: rotate(8deg);
    transform: rotate(8deg);
    font-size: 10px;
    font-weight: 600;
    line-height: 18px;
    z-index: 99;
    color: #fff;
    transition: 0.3s;
  }

  .checkmark:after {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    content: "";
    width: 18px;
    height: 18px;
    background: transparent;
    border-radius: 3px;
    border: 1px solid #e9e7f7;
    transition: 0.3s;
    transform: scale(1);
  }
}

