  h5 {
    font-size: 16px;
    font-weight: 500;
    font-family: $font2;
    color: $theme_color;
    margin-bottom: 0;
  }
  .label_name {
    font-size: 16px;
    font-weight: 500;
    font-family: $font2;
    color: $theme_color;
    margin-bottom: 0;
  }
  &.white_checkbox {
    .checkmark:after {
      border-color: $white;
    }
  }
  .checkmark {
    position: relative;
    width: 18px;
    height: 18px;
    top: 0;
    left: 0;
    display: block;
    cursor: pointer;
    line-height: 18px;
    flex: 18px 0 0;
    border-radius: 50%;
  }
  // .checkmark:after {
  //     content: "";
  //     position: absolute;
  //     display: none;
  // }
  input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
  }
  input:checked ~ .checkmark:after {
    width: 100%;
    height: 100%;
    transition: 0.3s;
    transform: scale(1);
    border-color: $purple_color;
  }
  input:checked ~ .checkmark {
    // background: $purple_color;
    // width: 100%;
    // height: 100%;
    transition: 0.3s;
  }
  input:checked ~ .checkmark::before {
    transform: translate(-50%, -50%) scale(1);
  }
  .checkmark::before {
    position: absolute;
    background: $purple_color;
    content: "";
    width: 8px;
    height: 8px;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: 0.3s;
  }
  .checkmark:after {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    content: "";
    width: 18px;
    height: 18px;
    background: transparent;
    border-radius: 50%;
    border: 1px solid #e9e7f7;
    transition: 0.3s;
    transform: scale(1);
