.play_button {
  width: 100px;
  height: 100px;
  background: #fff;
  border-radius: 50%;
  color: $purple_color;
  display: inline-block;
  text-align: center;
  line-height: 100px;
  position: relative;
  font-size: 25px;
  transition: 0.3s;
  cursor: pointer;
  &:hover {
    background: $purple_color;
    color: $white;
  }
}

.theme__overlay {
  position: relative;
  z-index: 2;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0a0b21;
    opacity: 0.4;
    z-index: -1;
  }
}

.lms_tabmenu {
  background: #f8f8fe;
  border-radius: 10px;
  display: inline-flex;
  li {
    a {
      font-size: 16px;
      font-weight: 600;
      font-family: $font1;
      color: $theme_color;
      border-radius: 10px;
      padding: 13px 39px;
      @media #{$tab} {
        padding: 13px 30px;
      }
      @media #{$large_mobile} {
        padding: 13px 27px;
      }
      &.active {
        background: $purple_color;
        color: $white;
        border-radius: 10px;
      }
    }
  }
}

.theme_border {
  border-top: 1px solid #e9e7f7;
}

.theme_according {
  button {
    &::before {
      left: 25px;
      position: absolute;
      -webkit-transition: 0.4s;
      transition: 0.4s;
      content: "\e622";
      font-family: "themify";
      font-size: 16px;
      color: #373737;
      font-weight: 400;
    }
    &.collapsed::before {
      content: "\e61a";
      font-family: "themify";
    }
  }
}
.theme_according {
  .card {
    margin-bottom: 15px;
    border: 0;
    .card-header {
      border: 1px solid #e9e7f7;
      border-radius: 5px;
      background: transparent;
      padding: 0;

      h5 {
        button {
          font-family: $font1;
          font-size: 18px;
          font-weight: 600;
          color: $theme_color;
          display: flex;
          justify-content: space-between;
          width: 100%;
          border: 0;
          padding: 11.5px 25px 11.5px 50px;
          text-decoration: none !important;
          @media #{$tab} {
            flex-wrap: wrap;
          }
          span {
            font-size: 14px;
            font-weight: 500;
            font-family: $font1;
            color: #373737;
          }
        }
      }
    }
  }
  .card-body {
    padding: 0;
    padding-top: 20px;
    .curriculam_list {
      .curriculam_single {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        align-items: center;
        @media #{$tab} {
          flex-wrap: wrap;
        }
        &:last-child {
          margin-bottom: 15px;
        }
        .curriculam_left {
          display: flex;
          align-items: center;
          @media #{$tab} {
            margin-bottom: 10px;
          }
          i {
            font-size: 18px;
            font-weight: 400;
            margin-right: 15px;
          }
          span {
            font-size: 16px;
            font-weight: 500;
            color: #373737;
          }
        }
        .curriculam_right {
          display: flex;
          align-items: center;

          .theme_btn_lite {
            margin-right: 30px;
          }
          span {
            font-size: 14px;
            font-weight: 500;
            color: #373737;
          }
        }
      }
    }
  }
}
